import React, {useState} from 'react'
import Axios from 'axios'


function ViewResultsPage( {name, addData, error, viewed} ) {
    return (
        <div className = "admin"> 
            <h2>View Ratings for <span>{name}</span>:</h2>
            <h2></h2>
            {/* <h3>Rate here! (scale 1 to 5) </h3> */}
            <h2></h2>
            { (error != "") ? (<div className=' error'><h4>{error}</h4></div> ) : "" }
            <h2></h2>
            <button onClick = {viewed}>View Data</button>
            {/* <div>DDDDDDDDDDDDDDDDDD</div> */}

        </div>
    )


}

export default ViewResultsPage